@import-normalize; /* bring in normalize.css styles */

:root {
  --primary-font: 'Manrope', sans-serif;
  --second-font: 'Inter', sans-serif;
  --third-font: 'Poppins', sans-serif;
  --dark-color: #000000;
  --orange-color: #ffc107;
  --blue-color: #54adff;
  --white-color: ##ffffff;
  --blue-light-color: #cce4fb;
  --fon-color: #fef9f9;
  --overlay-color: #2b2b2b;
  --green-color: #00c3ad;
  --red-color: #f43f5e;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

h1,
h2,
h3,
h4,
h5,
h6,
ul,
p,
li {
  margin: 0;
}

ul {
  padding: 0;
  list-style: none;
}

a {
  display: block;
  text-decoration: none;
  color: inherit;
}

img {
  max-width: 100%;
  display: block;
}

input,
button,
select {
  font: inherit;
}

body {
  margin: 0;
  /* font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif; */
  font-family: var(--primary-font);
  /* -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; */

  margin-left: auto;
  margin-right: auto;

  /* background-blend-mode: multiply;
  background-image: url(/src/images/bg-desctop.jpg);
  background-position: center;
  background-size: cover; */
  background-color: #fef9f9;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@media screen and (min-width: 280px) {
  body {
    padding: 20px;
    background-blend-mode: multiply;
    background-image: url(/src/images/bg-mobile.jpg);
    background-position: center;
    background-size: cover;
  }
}

@media screen and (min-width: 768px) {
  body {
    padding: 24px 32px;
    background-blend-mode: multiply;
    background-image: url(/src/images/bg-tablet.jpg);
    background-position: center;
    background-size: cover;
  }
}

@media screen and (min-width: 1280px) {
  body {
    padding: 20px 16px;
    background-blend-mode: multiply;
    background-image: url(/src/images/bg-desctop.jpg);
    background-position: center;
    background-size: cover;
  }
}
